.page-header {
  padding: 32px 0 0 0;
  margin-bottom: 93px;

  background-color: $bg-primary-color;
  background-repeat: no-repeat;
  background-image: url("../img/bg-lines.png");
  background-size: cover;
}

@media (max-width: $desktop-width) {
  .page-header {
    padding: 21px 0;
    margin-bottom: 37px;
  }
}

@media (max-width: $tablet-width) {
  .page-header {
    padding: 10px 0;
    margin-bottom: 18px;
  }
}
